import React from 'react'
import logo from '../../logo.png';
import '../../App.css';
import { useState } from "react";
import axios from "axios";
import { useHistory } from "react-router-dom";


const Register = () => {
  const [popup, setShowText] = useState(false);
  let history = useHistory();
  const [username, setUsername] = useState("");
  const [password, setPassword] = useState("");

  //när popup knappen trycks leder den till login sidan
  const OK = () => {
    history.push("/");
  };

  //html som visas om server return status är 200(godkänd)
  const Text = () => <div className="popup"><div>Du har registert dig, gå till log in sidan!</div><div className="logout_div"><button type="submit" className="logout" onClick={OK}>OK</button></div></div>;

  //när sidan laddas kollar den om det finns registerat token på local 
  // storage, om det finns leder den till home page för att logga ut
  window.onload = function () {
    if (localStorage.getItem('Token')
      !== null) {
      history.push("/home");
    }
  }

  //Skickar användernamn och lösenort till servern.
  const registerasig = async e => {
    e.preventDefault();
    axios.post(
      "https://h-178-174-162-51.a536.priv.bahnhof.se/sign-up/", {

      username,
      password

    })

      //om serven returnerar status 200(godkänd) då visar den popup
      .then(function (response) {
        if (response.status === 200) {
          setShowText(true)
        }
      })
  };

  return (
    <div>
      <div className="con">
        <header className="App-header">
          <h1 className="h1">
            ITHSC
          </h1>
          <img src={logo} className="App-logo" alt="logo" />
          {popup ? <Text /> : null}


        </header>
       
        <div className="login-con">
          <form onSubmit={registerasig}>
            <div className="username-div">

              <label>
                <input type="email" placeholder="Email:" id="Mail" className="input" value={username}  onChange={({ target }) => setUsername(target.value)} required />
              </label>
            </div>


            <div className="Password-div">
              <label>
                <input type="password" placeholder="Password:" id="password" className="input" value={password} onChange={({ target }) => setPassword(target.value)} required />
              </label>
            </div>
            <div>
              <button type="submit" className="login-button">Registera</button>
            </div>
          </form>
        </div>
        <div>
        </div>
      </div>
    </div>
  )
};

export default Register
