import React from 'react'
import logo from '../../logo.png';
import '../../App.css';
import { useState } from "react";
import axios from "axios";
import { Link } from "react-router-dom";
import { useHistory } from "react-router-dom";



const Login = () => {

  let history = useHistory();
  const [username, setUsername] = useState("");
  const [password, setPassword] = useState("");

  //när sidan laddas kollar den om det finns registerat token på local 
  // storage, om det finns leder den till home page för att logga ut
  window.onload = function () {
    if (localStorage.getItem('Token')
      !== null) {
      history.push("/home");
    }
  }

  //Skickar användernamn och lösenort till servern.
  const loggain = async e => {
    e.preventDefault();
    let token = await axios.post(
      "https://h-178-174-162-51.a536.priv.bahnhof.se/auth/", {

      username,
      password

    });

    //sparar p localstorage och consol loggar access_token
    let data = token.data;
    console.log(data.access_token);
    localStorage.setItem('Token', data.access_token);
    let key = localStorage.getItem("Token")

    //när du loggar in leder den till home page, kollar om du har fått tokenet.
    if (key) {
      history.push("/home");


    }
  };

  return (
    <div>
      <div className="con">
        <header className="App-header">
          <h1 className="h1">
            ITHSC
          </h1>

          <img src={logo} className="App-logo" alt="logo" />


        </header>
        <div className="login-con">
          <form onSubmit={loggain}>
            <div className="username-div">

              <label>
                <input type="text" placeholder="Username:" value={username} id="username" className="input" onChange={({ target }) => setUsername(target.value)} required />
              </label>
            </div>
            <div className="Password-div">
              <label>
                <input type="password" placeholder="Password:" id="password" className="input" value={password} onChange={({ target }) => setPassword(target.value)} required />
              </label>
            </div>
            <div>
              <button type="submit" className="login-button">Login</button>

            </div>
          </form>
        </div>
        <p className="p">Forgot your Password?</p>
        <p className="p"><Link to="/register" className="alink"> New RegisterUser? Register Here </Link></p>

      </div>
    </div>
  )
};


export default Login


